export default {
	data() {
		return {
			productList:[],
			showNum:true
		};
	},
	onShow(){
		this.initData()
	},
	methods:{
		initData(){
			let self = this
			this.$acFrame.HttpService.prodRank().then(res=>{
				if(res.success){
					self.productList = res.data
				}
			})
		},
		setImg(src){
			return  this.$acFrame.Util.setImgUrl(src);
		},
	}
}
